////////////////
// Compact Nav
////////////////

@media (min-width:767px) {
    [data-sidebar-style="compact"] {

        .deznav {
            
            .nav-user{
                display: none;
            }
        
            .metismenu {
				& > li{
				
					& > a{
						padding:1.5rem 1.625rem 1.5rem 1.5rem;
					}
					ul{
						padding:1rem 0;
						
						li > a{
							padding:0.5rem 1rem;
							&.has-arrow:after{
								content: "";
							}
						}
					}
				}
                li {
                    text-align: center;
                    a {
                        
						svg{
						
							max-width: 21px;
							max-height: 21px;
							display: block;
							margin-left: auto;
							margin-right: auto;	
						}
						i{
							font-size:2.2rem;
						}
						&:after{
							content:none;
						}
						&:before{
							content: "";
							height: 75px;
							width: 0;
							position: absolute;
							left: 0;
							top: 50%;
							transform:translatey(-50%);
							background: var(--bs-primary);
							border-radius:0 var(--bs-border-radius) var(--bs-border-radius) 0;
							@include transitionMedium;
						}
                    }
					
					ul:after{
						content:none;
					}
					&.mm-active,
					&:hover{
						& > a{
							background: -moz-linear-gradient(left,  rgba(224,224,228,1) 0%, rgba(125,185,232,0) 100%); 
							background: -webkit-linear-gradient(left,  rgba(224,224,228,1) 0%,rgba(125,185,232,0) 100%); 
							background: linear-gradient(to right,  rgba(224,224,228,1) 0%,rgba(125,185,232,0) 100%);
							&:before{
								width:10px;
							}
						}
					}
                }
            }
        }

        .nav-text {
            display: inline-block;
			margin-top: 0.7125rem;
        }

        .nav-label {
            &.first{
                display: none;
            }
        }

        .nav-badge {
            display: none;
        }

        .footer {
            padding-left: 9.375rem;
        }

        .content-body {
            margin-left: 15rem;
        }
		&[data-layout="horizontal"]{
			.deznav {
				.metismenu {
					li {
						a {
							i{
								font-size:1.4rem;
							}
							&:after{
								content:none;
							}
							&:before{
								content: none;
							}
						}
						
						ul:after{
							content:none;
						}
						&.mm-active,
						&:hover{
							& > a{
								background:rgba(var(--bs-primary-rgb), 0.1);
							}
						}
					}
				}
			}
		}
    }
}
[data-layout="horizontal"][data-sidebar-style="compact"] {
	.footer {
		padding-left: 0;
	}
	.content-body {
		margin-left:0;
	}
	.deznav .metismenu{
	    padding: 0 30px;
	}
}
[data-layout="vertical"][data-sidebar-style="compact"] {
    .deznav {
        width: 15rem;
        
        .metismenu {
		
            ul{
				background: rgba(0,0,0,0.02);
				
				@at-root [data-theme-version="dark"]#{&} {
					background:(255, 255, 255, 0.05);
				}
					
				a{
					&:before{
						content:none;
					}
				}
				
				ul a {
					padding: 0.625rem 0.9375rem;
					
				}
            }

            &>li {
                &>a.has-arrow::after {
                    top: 1px;
					display: inline-block;
					right: auto;
					margin-left: 5px;
					position: relative;
					width: 7px;
					height: 7px;
					border-width: 2px 0px 0px 2px;
                }
            }
        }
    }
	&[data-bs-theme="dark"]{
		.deznav{
			.metismenu{
				li{
					&:hover{
						>a{
							background: linear-gradient(to right, rgb(100 100 100) 0%, rgba(125, 185, 232, 0) 100%);
						}
					}
					&.mm-active{
						>a{
							background: linear-gradient(to right, rgb(100 100 100) 0%, rgba(125, 185, 232, 0) 100%);
							color: var(--bs-white) !important;
						}
					}
				}
			}
		}
	}
}